<template>
  <div>
    <!-- 二级路由出口 -->
    <router-view></router-view>
    <van-tabbar v-model="active" active-color="orange">
      <van-tabbar-item icon="home-o" replace to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="apps-o" replace to="/cate">分类</van-tabbar-item>
      <van-tabbar-item icon="cart-o" replace to="/cart" :badge="sum"
        >购物车</van-tabbar-item
      >
      <van-tabbar-item icon="user-o" replace to="/mine">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      active: 0,
    };
  },
  methods: {
    ...mapActions({
      requestCartList: "cartListActions",
    }),
  },
  computed: {
    ...mapGetters({
      user: "user",
      sum: "sum",
    }),
  },
  mounted() {
    if (this.user) {
      this.requestCartList(this.user.uid);
    }
  },
};
</script>

<style>
</style>



